import React, { useContext } from 'react'
import Input from '../../../shared/components/FormElements/Input'
import Button from '../../../shared/components/FormElements/Button'
import './Forminput.css'
import { useRef } from 'react'
import useCustomhooks from '../../../shared/hooks/form-custom-hooks'
import { useHttpClient } from '../../../shared/hooks/http-hook'
import LoadingSpinner from '../../../shared/components/UIElements/LoadingSpinner'
import ErrorModal from '../../../shared/components/UIElements/ErrorModal'
import { useNavigate } from 'react-router-dom'
import { AuthContext } from '../../../shared/context/auth-context'

const Forminput = () => {  
  const { isLoading, error, sendRequest, clearError } = useHttpClient();
  const auth= useContext(AuthContext)
const navigate= useNavigate()
const{  
  value:nameinput,
  entervalueisvalid:enternameisvalid,
  haserror:nameinputisvalid,
  valuechangehandler:namechangehandler,
  valueblurhandler:nameblurhandler,
  reset:resetname
} =useCustomhooks(val=>val.trim() !=="")

const{  
  value:emailinput,
  entervalueisvalid:enteremailisvalid,
  haserror:emailinputisvalid,
  valuechangehandler:emailchangehandler,
  valueblurhandler:emailblurhandler,  
  reset:resetemail
} =useCustomhooks(val=>val.includes("@"))

const{  
  value:mobileinput,
  entervalueisvalid:entermobileisvalid,
  haserror:mobileinputisvalid,
  valuechangehandler:mobilechangehandler,
  valueblurhandler:mobileblurhandler,  
  reset:resetmobile
} =useCustomhooks(val=>val.trim() !=="")

const{  
  value:passwordinput,
  entervalueisvalid:enterpasswordisvalid,
  haserror:passwordinputisvalid,
  valuechangehandler:passwordchangehandler,
  valueblurhandler:passwordblurhandler,  
  reset:resetpassword
} = useCustomhooks(val=>val.trim() !=="")


const nameref= useRef()
const emailref= useRef()
const mobileref= useRef()
const passwordref= useRef()

let formisvalid= false
if(enternameisvalid && enteremailisvalid && entermobileisvalid && enterpasswordisvalid ){
  formisvalid=true
}


const submithandler= async(event)=>{
  event.preventDefault();
    // console.log(nameref.current.value)
    
if(!enternameisvalid){
  return;  
}
if(!enteremailisvalid){
  return;  
}
if(!entermobileisvalid){
  return;  
}
if(!enterpasswordisvalid){
  return;  
}
resetname()
resetemail()
resetmobile()
resetpassword()

try {
  const responseData = await sendRequest(
    process.env.REACT_APP_BACKEND_URL +'/users/signup',
    'POST',
    JSON.stringify({
      email: emailinput,
      name: nameinput,
      mobile: mobileinput,
      password: passwordinput
    }),
    {
      'Content-Type': 'application/json'
    }
  );
  // auth.login(responseData.user.id);
  auth.login(responseData.userId, responseData.token);
  navigate("/sellerlogin")
} catch (err) {}
}



  const nameinputonclasses= nameinputisvalid ? "form-control invalid" : "form-control"
  const mobileinputonclasses= mobileinputisvalid ? "form-control invalid" : "form-control"
  const emailinputonclasses= emailinputisvalid ? "form-control invalid" : "form-control"
  const passwordinputonclasses= passwordinputisvalid ? "form-control invalid" : "form-control"
  return (
    <>    
    <ErrorModal error={error} onClear={clearError} />
        {isLoading && <LoadingSpinner asOverlay />}

        {
          !auth.isLoggedIn && <h2>New Admin Registration</h2>
        }
        

        {
          auth.isLoggedIn && <h2 style={{textDecoration:"none"}}>Hello {auth.userName}</h2>
        }
       

    <div className='form_input_div'>

    {/* {
      !auth.isLoggedIn &&
     */}
    <form onSubmit={submithandler}>

      <div className={nameinputonclasses}>
      <Input
      id="NAME"
      type='text'
        ref={nameref}
        onChange={namechangehandler}
        onBlur={nameblurhandler}
        value={nameinput}
      />
      {nameinputisvalid && <p>enter valid name</p>}
      </div>

      <div className={emailinputonclasses}>
      <Input
      id="EMAIL"
      type= 'email'
        ref={emailref}
        onChange={emailchangehandler}
        onBlur={emailblurhandler}
        value={emailinput}
      />
      {emailinputisvalid && <p>enter valid email</p>}
      </div>
      
      
      <div className={mobileinputonclasses}>
      <Input
      id="MOBILE"
      type='number'
        ref={mobileref}
        onChange={mobilechangehandler}
        onBlur={mobileblurhandler}
        value={mobileinput}
      />
      {mobileinputisvalid && <p>enter valid mobile</p>}
      </div>

      <div className={passwordinputonclasses}>
      <Input
      id="PASSWORD"
      type='password'
        ref={passwordref}
        onChange={passwordchangehandler}
        onBlur={passwordblurhandler}
        value={passwordinput}
      />
      {passwordinputisvalid && <p>enter valid password</p>}
      </div>
      <Button disabled={!formisvalid}>Signup</Button>
      
      {!auth.isLoggedIn &&<Button to ='/sellerlogin'>Login</Button>}
      </form>
    {/* } */}
      
    </div><br/>
    
    <div className='div_button'>
      
      {
        auth.isLoggedIn && <Button to={`/${auth.userId}/sellerupdate`}>Edit Seller</Button>
      }
      {
        auth.isLoggedIn && <Button to='/sellernewproduct'>Upload Products</Button>
      }
      {
        auth.isLoggedIn && <Button to ={`/${auth.userId}/selleralluploadedproduct`}>View Uploaded Products</Button>
      }
      {
        auth.isLoggedIn && <Button to ={`/${auth.userId}/productcreator`}>View Order</Button>
      }
      </div>
      

    </>
  )
}

export default Forminput
